import React, { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet';

import { DateHumaniser } from '../common';

import styles from './NewsDetail.module.css';
import { getJson } from '../../services/httpService';

function NewsDetail(props) {

    const [newsItem, setNewsItem] = useState({});

    useEffect(() => {
        getJson(`article/${props.match.params.id}`)
            .then(item => {
                setNewsItem(item);
                window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
            })
            .catch(error => console.error(error)); // todo: log error
    }, []);

    return (
        <div className={styles["news-content-container"]}>
            <Helmet>
                <link rel="canonical" href={newsItem.SourceURL} />
            </Helmet>
            <h1>{newsItem.Title}</h1>
            <div className={styles['news-metadata'] + ' row'}>
                <div className="col-6 text-left">
                    <img alt="" src={'https://api.vaanoli.kodebot.com/news/images/feed-sources/' + newsItem.Source + '.png'} />
                </div>
                <div className="col-6 text-right align-self-center">
                    <DateHumaniser value={newsItem.PublishedAt} />
                </div>

            </div>
            <div className={styles["news-content"]} dangerouslySetInnerHTML={{ __html: newsItem.Content }} />
            <a className="btn btn-primary my-2 my-sm-0" target="_blank" rel="noopener noreferrer" href={newsItem.SourceURL} >Read original article</a>
        </div>
    );

}

export default NewsDetail;